import React, {Component} from "react"
import { connect } from "react-redux"
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import createBrowserHistory from "history/createBrowserHistory";
const history = createBrowserHistory();
class Checkin extends Component{
	constructor(props){
		super(props)
		this.state = {
			email: "",
			name: "",
			login: "",
			password: ""
		}
	}

	change = {
		email: (event) => {this.setState({email: event.target.value})},
		name: (event) => {this.setState({name: event.target.value})},
		login: (event) => {this.setState({login: event.target.value})},
		password: (event) => {this.setState({password: event.target.value})}
	}
	render(){
		return(		
			<form id = "checkIn" className = "container-fluid">
			<link to = "/users"></link>
			<button>  Facebook</button>
			<div className = "lines"><hr className = "line" /><span className = "line-Word"></span><hr className = "line"  /></div>
			<input 
				type = "email"  
				className = "email" 
				value = {this.state.email} 
				placeholder = ".   . " 
				onChange = {this.change.email}
			/>
			<input 
				type = "text" 
				className = "name" 
				value = {this.state.name}
				 placeholder = "  " 
				 onChange = {this.change.name}
			/>
			<input 
				type = "text" 
				className = "login" 
				value = {this.state.login} 
				placeholder = " " 
				onChange = {this.change.login}
			/>
			<input 
				type = "password" 
				className = "password" 
				value = {this.state.password} 
				placeholder = "" 
				onChange = {this.change.password}
			/>	
			<p className = "errCheck">     </p>
			<Link  to = "/users" className = "checkIn-button" onClick = {
				(e) => { 
						
					let verific = this.props.changeData({
						id: this.props.lengthUsers ,
						name: this.state.name, 
						email: this.state.email, 
						login: this.state.login, 
						password: this.state.password
						})
					
					if(verific == "err"){
						e.preventDefault();
						document.getElementsByClassName("errCheck")[0].style.display = "block";
						console.log("!!!!!!!", document.getElementsByClassName("errCheck")[0].style);
					}
					
					
					
				}
			}></Link>
			<div>,    ,         cookie.</div>
			<div>{this.props.children}</div>
			</form>	
			)
	}
}
export default Checkin


